*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

.timer {
  display: flex;
  justify-content: center;

  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.field {
  display: flex;
  flex-direction: column;

  margin-left: 4px;
  margin-right: 4px;

  background-color: #343536;
  color: #ffffff;
  padding: 20px 30px;
  border-radius: 5px;
}

.value {
  font-size: 30px;
}

.label {
  text-transform: uppercase;
  font-size: 14px;
}
